<template>
  <div class="about">
    <h1>This is an about page</h1>
    <div id="video-container">

    </div>
    <div style="font-size: 30px" @click="$router.back()">播放视频后点击这里退出</div>
  </div>
</template>


<script>
  // @ is an alias to /src
  import HelloWorld from '@/components/HelloWorld.vue'

  export default {
    name: 'home',
    data() {
      return {
        vHeight: 200
      }
    },
    components: {
      HelloWorld
