        'vid=6E13447B323974CA9C33DC5901307461' +
        '&siteid=E45391D73C11AC7C' +
        '&autoStart=true' +
        '&width=100%' +
        '&height=' + this.vHeight +
        '&playerid=1CD72E6147C20106' +
        '&playertype=1'
        + '&banDrag=true'
      s.type = 'text/javascript'
      // console.log('载入新视频')
      document.getElementById('video-container').appendChild(s)
    }
  }
</script>
